html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input,textarea,select,button{font-family:inherit}textarea{resize:none}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}html{height:100%;font-family:'Open Sans', sans-serif;color:#636363}body{height:100%;background:linear-gradient(233.1deg, #216190 19.65%, #0834d0 81.25%);background-repeat:no-repeat;background-attachment:fixed}

.App{max-width:600px;margin:0 auto;width:calc(100% - 40px)}@media screen and (max-width: 360px){.App{width:calc(100% - 20px)}}.App__Title{padding-top:20px;text-align:center;font-weight:700;font-size:40px;line-height:64px;color:white}@media screen and (max-width: 680px){.App__Title{font-size:34px}}@media screen and (max-width: 480px){.App__Title{font-size:28px}}.App__Progress{margin-top:20px;text-align:center;color:white}

.FormProgress{position:relative;display:flex;justify-content:space-between;align-items:center;margin:20px 65px 50px}@media screen and (max-width: 680px){.FormProgress{margin:15px 65px 30px}}.FormProgress__Circle{color:white;height:30px;width:30px;text-align:center;line-height:30px;background-color:#1E34A5;border-radius:50%;font-size:18px;font-weight:700;z-index:2}.FormProgress__Circle--active{background-color:#DA3F5B}.FormProgress__Circle--disabled{background-color:#A7A7A7}.FormProgress__Line{flex-grow:4;margin:0 9px;height:2px;opacity:0.2;border-radius:2px;background-color:#e8e8e8}

.Form{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:30px;gap:30px;grid-template-areas:'company-name company-name number-of-people' 'company-area company-area company-area' 'description description description' 'input-file input-file input-file' 'submit . .';padding:55px;margin-bottom:20px;border-radius:8px;background-color:#ffffff}@media screen and (max-width: 680px){.Form{grid-template-columns:1fr;grid-template-areas:'company-name' 'number-of-people' 'company-area' 'description' 'input-file' 'submit'}}@media screen and (max-width: 480px){.Form{padding:30px}}.Form__Sumbit{grid-area:submit;padding:16px 0;font-size:700;font-size:16px;line-height:22px;border-radius:8px;border-color:transparent;background-color:#DA3F5B;color:white;transition:background-color 300ms,box-shadow 300ms;cursor:pointer;outline:none;box-shadow:none}.Form__Sumbit:focus{box-shadow:0 0 0 3px rgba(193,37,65,0.5)}.Form__Sumbit:hover{background-color:#c12541}

.FormField{position:relative}.FormField__Label{font-size:14px;font-weight:600;line-height:100%}.FormField__Required{margin-left:3px;color:#F15557}.FormField__Input{display:inline-block;margin-top:8px;padding:8px 16px;height:48px;border:1px solid transparent;border-radius:8px;background-color:#F9F9F9;box-sizing:border-box;width:100%;transition:box-shadow 300ms;outline:none;box-shadow:none}.FormField__Input:focus{box-shadow:0 0 0 3px rgba(0,123,255,0.5)}.FormField__Input::-webkit-input-placeholder{color:#A7A7A7}.FormField__Input:-ms-input-placeholder{color:#A7A7A7}.FormField__Input::-ms-input-placeholder{color:#A7A7A7}.FormField__Input::placeholder{color:#A7A7A7}.FormField__Input--TextArea{height:160px;padding:16px}.FormField__Input--Error{border-color:#F15557;box-shadow:0;transition:box-shadow 300ms;outline:none;box-shadow:none}.FormField__Input--Error:focus{box-shadow:0 0 0 3px rgba(241,85,87,0.5)}.FormField__ErrorMessage{position:absolute;height:32px;bottom:-36px;left:0;right:0;color:#F15557;font-size:12px;line-height:16px;-webkit-animation:error-appear 300ms;animation:error-appear 300ms}.FormField--CompanyName{grid-area:company-name}.FormField--NumberOfPeople{grid-area:number-of-people}.FormField--CompanyArea{grid-area:company-area}.FormField--Description{grid-area:description}@-webkit-keyframes error-appear{0%{opacity:0}100%{opacity:1}}@keyframes error-appear{0%{opacity:0}100%{opacity:1}}

.InputFile{grid-area:input-file}.InputFile__Label{display:flex;justify-content:space-between;align-items:center;border-radius:10px;border:1px solid rgba(51,51,51,0.2);padding:15px 24px 15px 60px;box-sizing:border-box;width:100%;font-size:14px;text-align:left;cursor:pointer;outline:none;box-shadow:none;background:url(data:image/png;base64,) no-repeat;background-position:left 24px center}.InputFile__Button{opacity:0;width:1px;height:1px;nav-index:-1}.InputFile__FilesAttached{font-size:14px;color:#a7a7a7}

